<template>
  <div class="component-ground font-coding" :style="state.style">
    <Webview ref="webviewRef" :page="store.state.page.name" :postback="postback" v-if="store.state.page.name" />
    <Home :login="store.state.account.login" :device="device" :callback="callback" :visible="state.visible.console" v-else />
    <Console :callback="callback" ref="consoleRef" v-if="state.visible.console" />
    <div class="modals" :class="state.modal.size" v-if="state.modal.name" @click="closeModal($event)">
      <div class="wrapper">
        <div class="inner">
          <Join :callback="callback" v-if="state.modal.name === 'join'" />
          <Login :callback="callback" v-else-if="state.modal.name === 'login'" />
          <Comment v-else-if="state.modal.name === 'comment'" />
          <ComponentViewer :callback="callback" :data="state.componentViewer" v-else-if="state.modal.name === 'componentViewer'" ref="componentViewerRef" />
          <ImageViewer :data="state.imageViewer" v-else-if="state.modal.name === 'imageViewer'" ref="imageViewerRef" />
        </div>
        <span class="close-btn" title="Close modal">&times;</span>
      </div>
    </div>
  </div>
</template>
<script>
import { reactive, ref } from "@vue/reactivity";
import { nextTick, onMounted } from "@vue/runtime-core";
import { useStore } from "vuex";
import Console from "./Console";
import Webview from "./Webview";
import Home from "./Home";
import Join from "../modals/Join";
import Login from "../modals/Login";
import Comment from "../modals/Comment";
import ComponentViewer from "../modals/ComponentViewer";
import ImageViewer from "../modals/ImageViewer";
import commLib from "../libs/commonLib";
import httpLib from "../libs/httpLib";

export default {
  components: { Console, Webview, Join, Login, Comment, ComponentViewer, ImageViewer, Home },
  setup() {
    const store = useStore();
    const state = reactive({
      visible: {
        console: false,
      },
      style: {
        paddingBottom: 0,
        paddingLeft: 0,
      },
      modal: {
        name: "",
        size: "normal",
      },
      imageViewer: {
        idx: 0,
        list: [],
      },
      componentViewer: {
        idx: 0,
        list: [],
      },
      template: {
        name: "",
      },
    });

    const consoleRef = ref(null);
    const webviewRef = ref(null);
    const componentViewerRef = ref(null);
    const imageViewerRef = ref(null);

    const consoleVisible = commLib.cookie.get("consoleVisible");
    const webviewTag = commLib.cookie.get("webviewTag");
    const webviewOutline = commLib.cookie.get("webviewOutline");
    const device = /Android|webOS|iPhone|iPad|Mac|Macintosh|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent) ? "mobile" : "desktop";

    const closeModal = (e) => {
      if (!state.modal.name) {
        return;
      }

      if (!e || e.target.classList.contains("modals") || e.target.classList.contains("wrapper") || e.target.classList.contains("close-btn")) {
        state.modal.name = "";

        nextTick(() => {
          consoleRef.value.commandRef.textareaRef.focus();
        });
      }
    };

    const checkLogin = (log) => {
      httpLib
        .get("/api/account/mid", { log })
        .then((res) => {
          store.state.account.login = false;

          if (res.data && Number(res.data) && commLib.isNumeric(res.data)) {
            store.state.account.mid = Number(res.data);
            store.state.account.login = true;
          } else {
            go();
          }
        })
        .catch(() => {
          commLib.message.show("error", "오류가 있습니다.");
        });
    };

    const go = (hash) => {
      if (hash) {
        location.hash = `/${hash}`;
      } else {
        location.hash = "";
        history.replaceState(null, null, " ");
      }
    };

    const watch = () => {
      const hash = location.hash;
      const hashArr = hash.split("/");
      const pageName = hashArr[1];
      const error = () => {
        commLib.message.show("warning", "입력하신 이름의 페이지가 존재하지 않습니다.");
        go();
      };

      store.commit("setPage", {
        ...store.state.page,
        name: null,
      });

      if (!hash || hashArr.length < 2) {
        return;
      }

      if (!pageName) {
        return;
      }

      state.visible.console = true;

      nextTick(() => {
        consoleRef.value.state.activeTab = consoleRef.value.tabs[0];

        httpLib
          .get(`/api/pages/details/${pageName}`)
          .then((res) => {
            const page = res.data;

            if (page && typeof page === "object" && Object.keys(page).length) {
              const seq = page.sequence;

              store.commit("setPage", {
                ...store.state.page,
                seq: seq,
                maxSeq: seq,
                name: page.name,
              });

              nextTick(() => {
                consoleRef.value.commandRef.state.numbers = page.numbers;
                consoleRef.value.reloadComponents();
                closeModal();
              });
            } else {
              error();
            }
          })
          .catch(() => {
            error();
          });
      });
    };

    const refresh = () => {
      if (!store.state.page.name) {
        return;
      }

      setNodeId("root");
      webviewRef.value.reload();
    };

    const keydownAlt = (key) => {
      if (!state.visible.console) {
        return;
      }

      if (commLib.isNumeric(key)) {
        const keyNum = Number(key) - 1;

        if (consoleRef.value.tabs.length > keyNum) {
          consoleRef.value.selectTab(consoleRef.value.tabs[keyNum]);
        } else {
          return;
        }
      } else if (typeof key === "string") {
        if (key.toLowerCase() === "d") {
          consoleRef.value.fold();
        } else if (key.toLowerCase() === "f") {
          consoleRef.value.setFullScreen();
        } else if (key.toLowerCase() === "c") {
          consoleRef.value.commandRef.textareaRef.focus();
        } else if (key.toLowerCase() === "a") {
          consoleRef.value.changePosition();
        } else if (key.toLowerCase() === "r") {
          refresh();
        } else if (key.toLowerCase() === "v") {
          toggle();
        } else if (key.toLowerCase() === "z") {
          undo();
        } else if (key.toLowerCase() === "t") {
          switch (consoleRef.value?.state.activeTab) {
            case "components":
              consoleRef.value.componentBoxRef.focus();
              break;

            case "my-components":
              consoleRef.value.myComponentBoxRef.focus();
              break;

            case "templates":
              consoleRef.value.templateBoxRef.focus();
              break;

            case "my-templates":
              consoleRef.value.myTemplateBoxRef.focus();
              break;
          }
        } else {
          return;
        }
      } else {
        return;
      }

      closeModal();
    };

    const keydownAltShift = (key) => {
      if (!state.visible.console) {
        return;
      }

      if (consoleRef.value.state.activeTab === "code") {
        const arr = ["!", "@", "#"];
        const idx = arr.indexOf(key);

        if (idx >= 0) {
          consoleRef.value.editorRef.selectLang(consoleRef.value.editorRef.editorLangs[idx]);
        }
      }

      if (typeof key === "string") {
        if (key.toLowerCase() === "z") {
          redo();
        }
      }
    };

    const setNodeId = (nid) => {
      store.commit("setPage", {
        ...store.state.page,
        nid: nid,
      });

      if (consoleRef.value.state.activeTab === "code") {
        consoleRef.value.editorRef.setCodes();
      }
    };

    const undo = () => {
      if (!store.state.page.name) {
        return;
      }

      runCommand("undo");
    };

    const redo = () => {
      if (!store.state.page.name) {
        return;
      }

      runCommand("redo");
    };

    const runCommand = (command) => {
      consoleRef.value.commandRef.runCommands(command);
    };

    const toggle = () => {
      if (!store.state.page.name) {
        return;
      }

      const val = !store.state.toggle.tag;
      consoleRef.value.toggleTagRef.change(val);
      consoleRef.value.toggleOutlineRef.change(val);
    };

    const postback = (name, val1, val2, val3, val4) => {
      switch (name) {
        case "complete-command": {
          const text = consoleRef.value.commandRef.state.text;
          const textArr = text.trim().split(" ");

          textArr[textArr.length - 1] = val1;
          consoleRef.value.commandRef.state.text = textArr.join(" ");

          if (textArr.length === 1) {
            consoleRef.value.commandRef.state.text += " ";
          }
          return;
        }

        case "complete-capture-node": {
          const args = {
            nid: val1,
            page: store.state.page.name,
            seq: store.state.page.seq,
            base64image: val2,
            category: val3,
            num: val4,
          };

          for (let i in args) {
            if (!args[i]) {
              commLib.message.show("warning", "컴포넌트가 존재하지 않습니다.");
              return;
            }
          }

          const save = (force) => {
            if (force) {
              args.force = true;
            }

            httpLib
              .post("/api/components/save", args)
              .then(() => {
                commLib.message.show("success", "컴포넌트를 저장하였습니다.");

                if (args.nid === "root") {
                  consoleRef.value.myTemplateBoxRef.init(true);
                } else {
                  consoleRef.value.myComponentBoxRef.init(true);
                }
              })
              .catch((err) => {
                switch (err.response?.status) {
                  case 400:
                    commLib.message.show("error", "잘못된 요청입니다.");
                    break;

                  case 409:
                    if (confirm("이미 존재하는 컴포넌트입니다. 덮어쓰시겠습니까?")) {
                      save(true);
                    }
                    break;

                  default:
                    commLib.message.show("error", "오류가 있습니다.");
                    break;
                }
              });
          };

          save();
          return;
        }

        case "click-nid":
          consoleRef.value.commandRef.state.text = `@${val1} `;
          consoleRef.value.commandRef.textareaRef.focus();
          setNodeId(val1);
          return;

        case "dblclick-nid":
          consoleRef.value.selectTab("code");
          setNodeId(val1);
          return;

        case "keydown-alt":
          keydownAlt(val1);
          return;

        case "keydown-alt-shift":
          keydownAltShift(val1);
          return;
      }
    };

    const keyup = (key) => {
      switch (key) {
        case "ArrowRight":
          if (state.modal.name === "componentViewer") {
            componentViewerRef.value.move("next");
          } else if (state.modal.name === "imageViewer") {
            imageViewerRef.value.move("next");
          }
          break;

        case "ArrowLeft":
          if (state.modal.name === "componentViewer") {
            componentViewerRef.value.move("prev");
          } else if (state.modal.name === "imageViewer") {
            imageViewerRef.value.move("prev");
          }
          break;
      }
    };

    const setStyle = (obj) => {
      if (obj.paddingLeft !== undefined) {
        state.style.paddingLeft = obj.paddingLeft;
      }

      if (obj.paddingBottom !== undefined) {
        state.style.paddingBottom = obj.paddingBottom;
      }
    };

    const callback = (name, val1, val2, val3) => {
      switch (name) {
        case "go":
          go(val1);
          break;

        case "refresh":
          refresh();
          break;

        case "undo":
          undo();
          break;

        case "redo":
          redo();
          break;

        case "set-style":
          setStyle(val1);
          break;

        case "toggle":
          consoleRef.value.toggleTagRef.change(val1);
          consoleRef.value.toggleOutlineRef.change(val1);
          break;

        case "check-login":
          checkLogin();
          break;

        case "toggle-console":
          state.visible.console = !state.visible.console;

          if (state.visible.console) {
            commLib.cookie.set("consoleVisible", "Y");
          } else {
            setStyle({ paddingLeft: 0, paddingBottom: 0 });
            commLib.cookie.remove("consoleVisible");
          }
          break;

        case "open-modal":
          state.modal.name = val1;
          state.modal.size = val2 ? val2 : "normal";

          switch (state.modal.name) {
            case "componentViewer":
              state.componentViewer.idx = val3.idx;
              state.componentViewer.list = val3.list;
              break;

            case "imageViewer":
              state.imageViewer.idx = val3.idx;
              state.imageViewer.list = val3.list;
              break;

            case "template":
              state.template.name = val3;
              break;
          }

          nextTick(() => {
            setTimeout(() => {
              document.querySelector(":focus")?.blur();
            });
          });
          break;

        case "set-nid": {
          setNodeId(val1);
          break;
        }

        case "keydown-alt":
          keydownAlt(val1);
          return;

        case "keydown-alt-shift":
          keydownAltShift(val1);
          return;

        case "close-modal":
          closeModal();
          break;
      }
    };

    // created
    if (consoleVisible === "Y") {
      state.visible.console = true;
    }

    if (webviewTag === "N") {
      store.commit("setToggle", {
        tag: false,
      });
    }

    if (webviewOutline === "N") {
      store.commit("setToggle", {
        outline: false,
      });
    }

    window.addEventListener("keyup", (e) => {
      keyup(e.key);
    });

    window.addEventListener("keydown", (e) => {
      if (e.altKey) {
        if (e.shiftKey) {
          keydownAltShift(e.key);
        } else {
          keydownAlt(e.key);
        }

        e.preventDefault();
      } else if (e.key === "Escape") {
        if (state.modal.name) {
          closeModal();
        }
      }
    });

    window.onhashchange = () => {
      watch();
    };

    checkLogin(true);

    onMounted(() => {
      watch();
    });

    return { store, state, consoleRef, webviewRef, componentViewerRef, imageViewerRef, closeModal, postback, callback, device };
  },
};
</script>
<style lang="scss" scoped>
.component-ground {
  position: relative;
  height: 100%;

  .modals {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.85);
    padding: 100px;
    z-index: 999;

    > .wrapper {
      width: 420px;
      max-width: 100%;
      height: 100%;
      margin-left: auto;
      margin-right: auto;
      position: relative;

      > .close-btn {
        position: absolute;
        cursor: pointer;
        top: -50px;
        right: 0;
        font-size: 42px;
        line-height: 50px;
        width: 50px;
        color: #fff;
        text-align: center;
        background: rgba(0, 0, 0, 0.21);
        transition: background 0.25s;

        &:hover {
          background: rgba(0, 0, 0, 0.42);
        }
      }

      > .inner {
        max-height: 100%;
        background: #fff;
        overflow: auto;
      }
    }

    &.large {
      > .wrapper {
        width: 1140px;
      }
    }

    &.full {
      > .wrapper {
        width: 100%;
      }
    }
  }
}
</style>
