<template>
  <div class="modal-image-viewer">
    <img :src="state.slide.url" @load="setStyle($event)" />
    <div class="angle left" title="Slide to prev" :style="state.angleStyle" @click="move('prev')">
      <img src="/img/ico.angle.left.white.png" alt="Slide to prev" />
    </div>
    <div class="angle right" title="Slide to next" :style="state.angleStyle">
      <img src="/img/ico.angle.right.white.png" @click="move('next')" alt="Slide to next" />
    </div>
  </div>
</template>
<script>
import { reactive } from "@vue/reactivity";
import { onMounted } from "@vue/runtime-core";
export default {
  props: {
    data: Object,
  },
  setup(props) {
    const state = reactive({
      slide: {
        idx: 0,
        url: "",
        list: [],
      },
      angleStyle: {
        display: "none",
        top: 0,
      },
    });

    const setStyle = (e) => {
      state.angleStyle.display = "block";
      state.angleStyle.top = e.target.parentNode.offsetHeight / 2 + "px";
    };

    const move = (direction) => {
      if (direction === "next" && state.slide.idx < state.slide.list.length - 1) {
        state.slide.idx += 1;
      } else if (direction === "prev" && state.slide.idx > 0) {
        state.slide.idx -= 1;
      } else {
        return;
      }

      state.slide.url = `/img/preview.${state.slide.list[state.slide.idx]}.png`;
    };

    onMounted(() => {
      const idx = props.data.idx;
      const list = props.data.list;
      state.slide.idx = idx;
      state.slide.url = `/img/preview.${list[idx]}.png`;
      state.slide.list = list;
    });

    return { state, move, setStyle };
  },
};
</script>
<style lang="scss" scoped>
.modal-image-viewer {
  img {
    max-width: 100%;
  }

  .angle {
    position: absolute;
    top: 50%;
    cursor: pointer;
    margin-top: -33.5px;
    display: none;

    &.left {
      left: -75px;
    }

    &.right {
      right: -75px;
    }
  }
}
</style>
