<template>
  <div class="component-home" ref="rootRef">
    <nav class="font-poppins">
      <a href="/" class="brand font-parisienne">P</a>
      <ul>
        <li v-for="(n, idx) in state.navs" :class="{ active: n.active }" @click="move(n.name)" :key="idx">{{ n.name.substring(0, 1).toUpperCase() + n.name.substring(1) }}</li>
      </ul>
      <button class="btn" @click="callback('toggle-console')" :title="`${visible ? 'Close' : 'Open'} Console`">
        <span v-if="visible">CLOSE </span>
        <span v-else>OPEN </span>
        <span>CONSOLE</span>
      </button>
    </nav>
    <header>
      <div class="inner">
        <h1 class="font-parisienne color-point">Publessing</h1>
        <br />
        <h2 class="font-parisienne color-point">
          <b>Draw the UI with commands</b>
        </h2>
        <template v-if="login">
          <h3>
            Enter "<b>create startup</b>" <br />
            then "<b>open startup</b>"
          </h3>
        </template>
        <template v-else>
          <h3>
            Enter "<b>join</b>" to create account <br />
            or "<b>login</b>" to use the service
          </h3>
        </template>
        <button class="btn btn-point font-poppins" @click="callback('toggle-console')" :title="`${visible ? 'Close' : 'Open'} Console`">
          <span v-if="visible">CLOSE </span>
          <span v-else>OPEN </span>
          <span>CONSOLE</span>
        </button>
      </div>
    </header>
    <article>
      <div class="parallax first font-poppins">
        <div class="wrapper">
          <div class="slogun">Draw web pages quickly</div>
          <div class="desc">Publessing is a useful tool for web developers when drawing UI.</div>
        </div>
      </div>
      <div class="guide">
        <div class="wrapper">
          <div class="title font-poppins"># Guide</div>
          <div class="content">
            <h1 class="font-poppins">Getting Started</h1>
            <ul>
              <li>- 상단의 <b>OPEN CONSOLE</b> 버튼을 클릭합니다.</li>
              <li>- 명령창에 <b>join</b> 입력 후 엔터를 눌러 가입합니다.</li>
              <li>- 명령창에 <b>login</b> 입력 후 엔터를 눌러 로그인합니다.</li>
              <li>- 명령창에 <b>create startup</b> 입력 후 엔터를 눌러 페이지를 생성합니다.</li>
              <li>- 명령창에 <b>open startup1</b> 입력 후 엔터를 눌러 생성된 페이지를 오픈합니다.</li>
            </ul>
            <h1 class="font-poppins">Console Commands</h1>
            <table class="table">
              <thead>
                <tr>
                  <th>Command</th>
                  <th>Description</th>
                  <th>Arguments</th>
                  <th>Example</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(c, idx) in commands.console" :key="idx">
                  <td>{{ c.cmd }}</td>
                  <td class="desc">{{ c.desc }}</td>
                  <td>{{ c.args.join(" ") }}</td>
                  <td>{{ c.example }}</td>
                </tr>
              </tbody>
            </table>
            <h1 class="font-poppins">Component Commands</h1>
            <table class="table">
              <thead>
                <tr>
                  <th>Command</th>
                  <th>Description</th>
                  <th>Arguments</th>
                  <th>Example</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(c, idx) in commands.node" :key="idx">
                  <td>{{ c.cmd }}</td>
                  <td class="desc">{{ c.desc }}</td>
                  <td>{{ c.args.join(" ") }}</td>
                  <td>{{ c.example }}</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
      <div class="parallax second font-poppins">
        <div class="wrapper">
          <div class="slogun">Complete web pages elegantly</div>
          <div class="desc">{{ state.counts.component }} components and {{ state.counts.template }} templates help you draw UI easily.</div>
        </div>
      </div>
      <div class="license">
        <div class="wrapper">
          <div class="title font-poppins"># License</div>
          <div class="content">
            <h1 class="font-poppins">Open source</h1>
            <div>퍼블레싱은 오픈소스를 지향합니다. 개인과 기업 등 모든 곳에서 무료로 이용하실 수 있습니다.</div>
            <div>
              <span>그리고 컴포넌트와 템플릿 코드는 </span>
              <a href="https://opensource.org/licenses/MIT" target="_blank" rel="noopener">MIT 라이선스</a>
              <span>로 자유롭게 활용하실 수 있습니다.</span>
            </div>
            <h1 class="font-poppins">MIT LICENSE</h1>
            <div>Copyright (c) 2021 Publessing</div>
            <br />
            <div>
              Permission is hereby granted, free of charge, to any person obtaining a copy of this software and associated documentation files (the "Software"), to deal in the Software without restriction, including without limitation the rights to
              use, copy, modify, merge, publish, distribute, sublicense, and/or sell copies of the Software, and to permit persons to whom the Software is furnished to do so, subject to the following conditions:
            </div>
            <br />
            <div>The above copyright notice and this permission notice shall be included in all copies or substantial portions of the Software.</div>
            <br />
            <div>
              THE SOFTWARE IS PROVIDED "AS IS", WITHOUT WARRANTY OF ANY KIND, EXPRESS OR IMPLIED, INCLUDING BUT NOT LIMITED TO THE WARRANTIES OF MERCHANTABILITY, FITNESS FOR A PARTICULAR PURPOSE AND NONINFRINGEMENT. IN NO EVENT SHALL THE AUTHORS OR
              COPYRIGHT HOLDERS BE LIABLE FOR ANY CLAIM, DAMAGES OR OTHER LIABILITY, WHETHER IN AN ACTION OF CONTRACT, TORT OR OTHERWISE, ARISING FROM, OUT OF OR IN CONNECTION WITH THE SOFTWARE OR THE USE OR OTHER DEALINGS IN THE SOFTWARE.
            </div>
            <h1 class="font-poppins">Notice</h1>
            <div>이 사이트는 다음의 오픈소스를 활용하여 제작하였습니다.</div>
            <ul>
              <li v-for="(d, idx) in dependencies" :key="idx">
                <a :href="d.url" target="_blank" rel="noopener">{{ d.name }}</a>
              </li>
            </ul>
          </div>
        </div>
      </div>
      <div class="parallax third font-poppins">
        <div class="wrapper">
          <div class="slogun">Feel free to use code</div>
          <div class="desc">Components and templates codes are under the MIT license.</div>
        </div>
      </div>
      <div class="contact">
        <div class="wrapper">
          <div class="title font-poppins"># Contact</div>
          <div class="content">
            <div class="row">
              <div class="col">
                <div class="form-group message">
                  <label class="font-poppins" for="contactMessage">
                    <span>Message </span>
                    <span class="color-point">*</span>
                    <span class="float-right">({{ state.contact.message.length }} / 1000)</span>
                  </label>
                  <textarea class="form-control focus-border-pointer" id="contactMessage" placeholder="Hey! It's cool!" @input="state.contact.message = $event.target.value" :disabled="state.loaded.contact ? false : true"></textarea>
                </div>
              </div>
              <div class="col">
                <div class="name form-group">
                  <label class="font-poppins" for="contactName">Name</label>
                  <input type="text" class="form-control focus-border-pointer" id="contactName" placeholder="James" v-model="state.contact.name" @keyup.enter="send()" :disabled="state.loaded.contact ? false : true" />
                </div>
                <div class="email form-group">
                  <label class="font-poppins" for="contactEmail">
                    <span>Email </span>
                    <span class="color-point">*</span>
                  </label>
                  <input type="email" class="form-control focus-border-pointer" id="contactEmail" placeholder="name@example.com" v-model="state.contact.email" @keyup.enter="send()" :disabled="state.loaded.contact ? false : true" />
                </div>
                <div class="satisfaction form-group">
                  <label class="font-poppins" for="contactSatisfaction">
                    <span>Satisfaction </span>
                    <span class="color-point">*</span>
                    <span class="float-right">{{ state.contact.comment }}</span>
                  </label>
                  <div class="icons row">
                    <div class="col text-right">
                      <i class="fa fa-frown-o" :class="{ 'active color-point': state.contact.point === '1', disabled: !state.loaded.contact }" @click="point('1')"></i>
                    </div>
                    <div class="col text-center">
                      <i class="fa fa-meh-o" :class="{ 'active color-point': state.contact.point === '2', disabled: !state.loaded.contact }" @click="point('2')"></i>
                    </div>
                    <div class="col">
                      <i class="fa fa-smile-o" :class="{ 'active color-point': state.contact.point === '3', disabled: !state.loaded.contact }" @click="point('3')"></i>
                    </div>
                  </div>
                </div>
                <div class="action">
                  <button class="btn btn-point font-poppins" @click="send()" :disabled="state.loaded.contact ? false : true">
                    <span>{{ `Send${state.loaded.contact ? "" : "ing..."}` }}</span>
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </article>
    <footer>
      <div class="wrapper">
        <div class="copyright">&copy; 2021. Publessing. All rights reserved.</div>
      </div>
    </footer>
    <button class="climb font-poppins btn color-point border-point" @click="move('home')">TOP</button>
  </div>
</template>
<script>
import { computed, onMounted, reactive, ref } from "@vue/runtime-core";
import defines from "../defines";
import $ from "jquery";
import commLib from "../libs/commonLib";
import httpLib from "../libs/httpLib";

export default {
  props: {
    login: Boolean,
    visible: Boolean,
    callback: Function,
  },
  setup() {
    const rootRef = ref();
    const state = reactive({
      navs: [
        { name: "home", active: true },
        { name: "guide", active: false },
        { name: "license", active: false },
        { name: "contact", active: false },
      ],
      counts: {
        component: 0,
        template: 0,
      },
      loaded: {
        contact: true,
      },
      contact: {
        name: "",
        email: "",
        message: "",
        point: "3",
        comment: computed(() => {
          if (state.contact.point === "3") {
            return "It's Cool";
          } else if (state.contact.point === "2") {
            return "Not bad";
          } else {
            return "Bad";
          }
        }),
      },
    });

    const previews = ["command", "code", "files", "components"];
    const definedCommands = defines.commands;
    const dependencies = [
      { name: "nodejs", url: "https://nodejs.org/" },
      { name: "bootstrap", url: "https://getbootstrap.com/" },
      { name: "fontawesome", url: "https://fontawesome.com/" },

      { name: "vue", url: "https://www.npmjs.com/package/vue" },
      { name: "vuex", url: "https://www.npmjs.com/package/vuex" },
      { name: "axios", url: "https://www.npmjs.com/package/axios" },
      { name: "core-js", url: "https://www.npmjs.com/package/core-js" },
      { name: "jquery", url: "https://www.npmjs.com/package/jquery" },
      { name: "babel-eslint", url: "https://www.npmjs.com/package/babel-eslint" },
      { name: "eslint", url: "https://www.npmjs.com/package/eslint" },
      { name: "eslint-plugin-vue", url: "https://www.npmjs.com/package/eslint-plugin-vue" },
      { name: "sass", url: "https://www.npmjs.com/package/sass" },
      { name: "sass-loader", url: "https://www.npmjs.com/package/sass-loader" },

      { name: "cookie-parser", url: "https://www.npmjs.com/package/cookie-parser" },
      { name: "cors", url: "https://www.npmjs.com/package/cors" },
      { name: "dotenv", url: "https://www.npmjs.com/package/dotenv" },
      { name: "ejs", url: "https://www.npmjs.com/package/ejs" },
      { name: "express", url: "https://www.npmjs.com/package/express" },
      { name: "js-beautify", url: "https://www.npmjs.com/package/js-beautify" },
      { name: "jsdom", url: "https://www.npmjs.com/package/jsdom" },
      { name: "jsonwebtoken", url: "https://www.npmjs.com/package/jsonwebtoken" },
      { name: "jszip", url: "https://www.npmjs.com/package/jszip" },
      { name: "jszip-utils", url: "https://www.npmjs.com/package/jszip-utils" },
      { name: "multer", url: "https://www.npmjs.com/package/multer" },
      { name: "mysql2", url: "https://www.npmjs.com/package/mysql2" },
      { name: "nodemailer", url: "https://www.npmjs.com/package/nodemailer" },
      { name: "sequelize", url: "https://www.npmjs.com/package/sequelize" },
      { name: "nodemon", url: "https://www.npmjs.com/package/nodemon" },
      { name: "@vue/cli-plugin-babel", url: "https://www.npmjs.com/package/@vue/cli-plugin-babel" },
      { name: "@vue/cli-plugin-eslint", url: "https://www.npmjs.com/package/@vue/cli-plugin-eslint" },
      { name: "@vue/cli-service", url: "https://www.npmjs.com/package/@vue/cli-service" },
      { name: "@vue/compiler-sfc", url: "https://www.npmjs.com/package/@vue/compiler-sfc" },
      { name: "@monaco-editor/loader", url: "https://www.npmjs.com/package/@monaco-editor/loader" },
    ];

    const commands = {
      console: [],
      node: [],
    };

    const scrollTops = {
      home: 0,
      guide: 0,
      license: 0,
      contact: 0,
      parallax: 0,
    };

    const point = (point) => {
      if (!state.loaded.contact) {
        return;
      }

      state.contact.point = point;
    };

    const send = () => {
      if (!state.loaded.contact) {
        return;
      }

      if (!state.contact.message) {
        commLib.message.show("warning", "내용을 입력해주세요.");
        document.querySelector("#contactMessage").focus();
      } else if (!state.contact.email) {
        commLib.message.show("warning", "이메일 주소를 입력해주세요.");
        document.querySelector("#contactEmail").focus();
      } else if (!commLib.isValidEmail(state.contact.email)) {
        commLib.message.show("warning", "이메일 주소의 형식이 올바르지 않습니다.");
        document.querySelector("#contactEmail").focus();
      } else if (state.contact.message.length > 1000) {
        commLib.message.show("warning", "메시지는 1000자를 넘을 수 없습니다.");
      } else if (state.contact.name && state.contact.name.length > 50) {
        commLib.message.show("warning", "이름은 50자를 넘을 수 없습니다.");
      } else if (state.contact.email.length > 100) {
        commLib.message.show("warning", "이메일 주소는 100자를 넘을 수 없습니다.");
      } else if (confirm("이메일을 발송하시겠습니까?")) {
        state.loaded.contact = false;

        httpLib
          .post("/api/email", state.contact)
          .then(() => {
            commLib.message.show("success", "이메일을 발송하였습니다.");
            state.loaded.contact = true;
            state.contact.name = "";
            state.contact.email = "";
            state.contact.message = "";
            state.contact.point = "3";
            document.querySelector("#contactMessage").value = "";
          })
          .catch(() => {
            commLib.message.show("error", "오류가 있습니다.");
            state.loaded.contact = true;
          });
      }
    };

    const setScrollTops = () => {
      const navHeight = rootRef.value.querySelector("nav").offsetHeight;
      scrollTops.guide = rootRef.value.querySelector("article > .guide").offsetTop - navHeight;
      scrollTops.license = rootRef.value.querySelector("article > .license").offsetTop - navHeight;
      scrollTops.contact = rootRef.value.querySelector("article > .contact").offsetTop - navHeight;
      scrollTops.parallax = rootRef.value.querySelector("article > .parallax.first").offsetTop - navHeight;
    };

    const move = (name) => {
      setScrollTops();

      if (scrollTops[name] === undefined) {
        return;
      }

      state.navs.forEach((x) => (x.active = false));
      state.navs.find((x) => x.name === name).active = true;

      $(rootRef.value)
        .stop()
        .animate({
          scrollTop: scrollTops[name],
        });
    };

    for (let i1 in definedCommands) {
      for (let i2 in definedCommands[i1]) {
        for (let c of definedCommands[i1][i2]) {
          commands[i1].push(c);
        }
      }
    }

    for (let i in commands) {
      commands[i].sort((a, b) => {
        if (a.cmd < b.cmd) {
          return -1;
        } else if (a.cmd > b.cmd) {
          return 1;
        }

        return 0;
      });
    }

    httpLib.get("/api/components/counts").then((res) => {
      if (res.data && Object.keys(res.data).length) {
        state.counts = res.data;
      }
    });

    onMounted(() => {
      let timer;
      setScrollTops();

      rootRef.value.addEventListener("scroll", function() {
        clearTimeout(timer);

        timer = setTimeout(() => {
          const nav = rootRef.value.querySelector("nav");
          const climb = rootRef.value.querySelector(".climb");
          const button = nav.querySelector("button");
          state.navs.forEach((x) => (x.active = false));

          if (rootRef.value.scrollTop > scrollTops.parallax) {
            button.classList.add("btn-point");
            climb.style.display = "inline-block";
            setTimeout(() => {
              climb.classList.add("on");
            });
          } else {
            button.classList.remove("btn-point");
            climb.style.display = "none";
            setTimeout(() => {
              climb.classList.remove("on");
            });
          }

          if (rootRef.value.scrollTop >= scrollTops.contact) {
            state.navs.find((x) => x.name === "contact").active = true;
          } else if (rootRef.value.scrollTop >= scrollTops.license) {
            state.navs.find((x) => x.name === "license").active = true;
          } else if (rootRef.value.scrollTop >= scrollTops.guide) {
            state.navs.find((x) => x.name === "guide").active = true;
          } else {
            state.navs.find((x) => x.name === "home").active = true;
          }
        }, 25);
      });
    });

    return { rootRef, state, previews, dependencies, move, point, send, commands };
  },
};
</script>
<style lang="scss" scoped>
.component-home {
  height: 100%;
  overflow: auto;
  position: relative;

  .wrapper {
    width: 1140px;
    margin: 0 auto;
    max-width: 100%;
    padding: 0 15px;
  }

  > nav {
    position: sticky;
    top: 0;
    left: 0;
    width: 100%;
    border-bottom: 1px solid #ddd;
    background: #f7f7f7;
    transition: background-color 0.25s;
    padding-left: 66px;
    z-index: 1;

    .brand {
      position: absolute;
      color: #000;
      top: 0;
      left: 0;
      font-size: 20px;
      cursor: pointer;
      font-weight: bold;
      text-decoration: none;
      border-right: 1px solid #ddd;
      height: 100%;
      width: 60px;
      text-align: center;
      line-height: 60px;
    }

    ul {
      list-style: none;
      margin: 0;
      padding: 0;

      li {
        cursor: pointer;
        display: inline-block;
        padding: 19px 10px;
        margin: 0 7.5px;
        font-size: 14px;

        &.active {
          font-weight: bold;
        }
      }
    }

    button {
      position: absolute;
      border-radius: 0;
      top: 6.5px;
      right: 7px;
      box-shadow: none;
      border: 1px solid #146ece;
      background: inherit;
      color: #146ece;
      padding: 11.5px 0;
      width: 150px;
      font-size: 14px;

      &:hover {
        background: #146ece;
        color: #fff;
      }
    }

    &.white {
      background: #fff;
    }
  }

  > header {
    width: 100%;
    height: 100%;
    max-height: 1080px;
    display: table;
    margin-top: -60px;
    position: relative;
    text-align: center;

    > .inner {
      display: table-cell;
      vertical-align: middle;

      > img {
        height: 100px;
        margin-bottom: 42px;
      }

      > h1 {
        font-size: 80px;
        font-weight: bold;
        display: inline-block;
        position: relative;
        margin-bottom: 25px;
      }

      > h2,
      > h3 {
        font-size: 28px;
      }

      h2 {
        margin-bottom: 60px;
      }

      > h3 {
        line-height: 45px;
      }

      > button {
        margin-top: 50px;
        width: 300px;
        height: 60px;
        line-height: 60px;
        font-size: 18px;
        padding: 0;
        box-shadow: none;
        outline: 0;
        border-radius: 0;
      }
    }
  }

  > article {
    min-height: 100%;

    .title {
      font-size: 24px;
      font-weight: bold;
    }

    > div {
      &.parallax {
        padding-top: 100px;
        padding-bottom: 100px;
        color: #fff;
        text-align: center;
        position: relative;

        .slogun {
          font-size: 42px;
          letter-spacing: 1px;
          margin-bottom: 20px;
          font-weight: bold;
        }

        .desc {
          font-size: 21px;
        }

        &:before {
          content: "";
          display: block;
          background-size: cover;
          background-attachment: fixed;
          position: absolute;
          left: 0;
          top: 0;
          width: 100%;
          height: 100%;
          filter: brightness(0.5);
          z-index: -1;
        }

        &.first {
          &:before {
            background-image: url(/img/starry-sky-1948523_1280.jpg);
          }
        }

        &.second {
          &:before {
            background-image: url(/img/watercolour-4819451_1280.jpg);
          }
        }

        &.third {
          &:before {
            background-image: url(/img/cutter-3202437_1280.jpg);
          }
        }
      }

      &.guide {
        padding-top: 42px;
        padding-bottom: 42px;

        h1 {
          font-size: 18px;
          padding-bottom: 15px;
          margin-top: 45px;
          margin-bottom: 0;
          border-bottom: 1px solid #ccc;
        }

        ul {
          margin: 15px 0 0 0;
          padding: 0;
          list-style: none;

          li {
            margin-bottom: 5px;
          }
        }

        table {
          border-bottom: 1px solid #ddd;

          th {
            border-top: 0;
            border-bottom-width: 1px;
            background: #eee;
          }

          td {
            vertical-align: middle;

            &.desc {
              font-size: 14px;
            }
          }
        }
      }

      &.license {
        padding-top: 42px;
        padding-bottom: 42px;

        .title {
          margin-bottom: 35px;
        }

        .content {
          font-family: initial;

          h1 {
            font-size: 18px;
            padding-bottom: 15px;
            margin-top: 45px;
            margin-bottom: 15px;
            border-bottom: 1px solid #ccc;
          }

          a {
            color: inherit;
          }

          ul {
            margin: 20px -5px 0 -5px;
            padding: 0;
            list-style: none;

            li {
              display: inline-block;
              margin-bottom: 10px;
              padding: 0 5px;

              > a {
                border: 1px solid #ddd;
